import '/styles/main.scss'
import ScrollMagic from "scrollmagic"
import ScrollToPlugin from "gsap/ScrollToPlugin";
import {
    TweenMax,
    TimelineMax,
    gsap,
    Expo,
    TweenLite
} from 'gsap'
import {
    ScrollMagicPluginGsap
} from 'scrollmagic-plugin-gsap'

gsap.registerPlugin(ScrollToPlugin);
ScrollMagicPluginGsap(ScrollMagic, TweenMax, TimelineMax)
import 'scrollmagic/scrollmagic/uncompressed/plugins/debug.addIndicators';



const link = document.getElementsByClassName('scroll')[0];


link.addEventListener('click', function (e) {
    e.preventDefault();
    const attrLink = link.getAttribute('href')

    gsap.to(window, {
        duration: 1.3,
        scrollTo: {
            y: attrLink,
            offsetY: 15
        }
    })

})


var controller = new ScrollMagic.Controller();
var tl = new TimelineMax();

tl.set(".header-card", {
    opacity: 0,
    x: 80
})
tl.set(".card", {
    opacity: 0,
    y: -40
})


window.onload = tl.to(".header-card", 1.5, {
    opacity: 1,
    x: 0,
    ease: Expo.easeOut,
}, 0.2)



var scene = new ScrollMagic.Scene({
        triggerElement: '.card-main',
        offset: 20,
        reverse: false,
    })

    .setTween(new TimelineMax().add([
        TweenLite.staggerTo(".card", 2, {
            opacity: 1,
            y: 0,
            ease: Expo.easeOut,
        }, 0.15),
    ]));


scene.addTo(controller);